@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap'); */
*{
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul{
  padding: 0;
}

li{
  list-style: none;
}
.mt-10{
  margin-top: 6rem;
}
.m-0-auto{
margin: 0 auto;
}

.titulo__Secundarios{
  font-size: 2rem;
  font-weight: 600;
  color: hsl(229, 31%, 21%)
}

.parrafo__Principal{
  color: hsl(229, 8%, 60%);
    line-height: 30px;
}

.buttons {
  display: flex;
  justify-content: flex-start;
}

.btn__PurpleLight {
  color: white;
  background-color: hsl(231, 69%, 60%);
  font-weight: bold;
}
.btn__PurpleLight:hover{
  color:hsl(231, 69%, 60%) ;
  background-color: white;
  font-weight: bold;
  border: 1px solid hsl(231, 69%, 60%);
}
.btn__OrangeLight{
  color: white;
  font-weight: bold;
  background-color: hsl(0, 94%, 66%);

}

.btn__OrangeLight:hover{
  color: hsl(0, 94%, 66%);
  background-color: white;
  font-weight: bold;
  border: 1px solid  hsl(0, 94%, 66%);
}

.btn__White {
  color: black;
  background-color: rgba(240, 240, 240, 0.9);
}

@media (max-width: 768px) {
  .buttons {
    justify-content: center;
  }
}

.contacts {
  background-color: hsl(231, 69%, 60%);
}
.contacts h6 {
  color: white;
  letter-spacing: 0.4rem;
  font-weight: bold;
  font-size: 0.8rem;
}
.contacts h1 {
  color: white;
  font-size: 35px;
  font-weight: bold;
  margin: 1rem 8rem;
}

@media (max-width: 768px) {
  .contacts div {
    width: 100% !important;
  }
}
@media (max-width: 425px) {
  .contacts div {
    width: 100% !important;
  }

  .contacts h1 {
    margin: 0;
  }
}

@media (max-width:425px) {
    .card{
        margin-top: 0!important;
    }
    
    button{
        display: block;
        margin: 0;
    }
}
.features__Menu {
  margin: 0 auto;
  width: 60%;
}
.features__Menu--List {
  width: 14rem;
}

.features__Menu--List li {
  text-align: center;
}
.features__Menu--Content {
  border-bottom: 3px solid hsl(0, 94%, 66%);
}

@media (max-width: 768px) {
  .features__Menu {
    width: 75%;
  }

  .features__Menu--List {
    width: 11rem;
  }
}

@media (max-width: 425px) {
  .features__Menu {
    margin: 0;
    width: 100%;
    flex-direction: column;
    align-items: center;
  }

  .features__Menu--List {
    width: 107%;
    margin-top: 1rem;
    border-bottom: 2px solid #ccc;
  }

  .features__Menu--Content {
    width: 14rem;
    margin: 0 auto;
  }
}

.infoFeature__Img::before {
  content: "";
  position: absolute;
  background-color: hsl(231, 69%, 60%);
  min-height: 19rem;
  width: 35.5%;
  top: 1026px;
  left: 0%;
  border-radius: 0 0 100px 0;
  z-index: -1;
}

.infoFeature__Img--Img {
  position: relative;
  top: 0;
  left: 0;
  width: 88%;
}

.infoFeature__Info {
  width: 35%;
}
.infoFeature__Info h1 {
  font-size: 2rem;
  font-weight: 600;
  color: hsl(229, 31%, 21%);
}

.infoFeature__Info p {
  color: hsl(229, 8%, 60%);
  line-height: 30px;
}

@media (max-width: 768px) {
  .infoFeature__Img::before {
    min-height: 17rem;
    top: 861px;
  }
}

@media (max-width: 425px) {
  .infoFeature__Img {
    min-height: 21rem;
  }
  .infoFeature__Img::before {
    top: 1281px;
    width: 63%;
    min-height: 15rem;
  }

  .infoFeature {
    flex-direction: column;
  }
  .infoFeature__Img--Img {
    width: 80%;
    position: relative;
    top: 0;
    left: 3rem;
  }
  .infoFeature__Info {
    width: 100%;
  }

  .infoFeature__Info h1 {
    font-size: 25px;
    text-align: center;
  }
}

@media (max-width: 375px) {
  .infoFeature__Img {
    min-height: 19rem;
  }
  .infoFeature__Img::before {
    min-height: 13rem;
  }
}

@media (max-width: 320px) {
  .infoFeature__Img {
    min-height: 17rem;
  }
  .infoFeature__Img::before {
    min-height: 10rem;
  }
}

.features__Parrafo {
  margin: 0 auto;
}

@media (max-width: 425px) {
  .features__Parrafo {
    margin: 0;
    width: 100% !important;
    font-size: 15px;
  }
}

footer {
  background-color: hsl(229, 31%, 21%);
  height: 6rem;
  padding: 0 10%;
  font-weight: bold;
}

.footer__BoxOne {
  width: 50%;
}

.footer__BoxOne ul {
  margin: 0;
  color: white;
}

.footer__BoxOne ul li {
  width: 6rem;
  text-align: center;
}
.footer__BoxOne ul li:hover {
  color: hsl(0, 94%, 66%);
}

.footer__BoxTwo {
  width: 10%;
}

.footer__BoxTwo ul {
  margin: 0;

  color: white;
}

.footer__BoxTwo ul li {
  font-size: 1.7rem;
}
@media (max-width: 425px) {
  footer {
    flex-direction: column;
    height: auto;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .footer__BoxOne {
    flex-direction: column !important;
  }
  .footer__BoxOne ul {
    margin-top: 2rem;
    flex-direction: column;
    align-items: center;
  }

  .footer__BoxOne ul li {
    line-height: 3rem;
  }

  .footer__BoxTwo {
    margin-top: 1rem;
    width: 30%;
  }
}

.card-header {
  border-bottom: none;
  background: white;
}

.card {
  border: none;
}

.line__Bottom {
  border-bottom: 0.5px solid #bbb;
}

.fa-chevron-down {
  color: hsl(231, 69%, 60%);
  font-size: 1.5rem;
}

@media (max-width: 425px) {
  .parrafo__Principal {
    width: 100% !important;
  }
  .accordion {
    width: 100% !important;
  }
}

.header__Img::before {
  content: "";
  position: absolute;
  top: 153px;
  background-color: hsl(231, 69%, 60%);
  min-height: 19rem;
  width: 78.5%;
  left: 37%;
  border-radius: 0 0 0 100px;
  z-index: -1;
}

.header__Info--P {
  font-size: 15px;
}

@media (max-width: 1140px) {
  .header__Img::before {
    width: 85.5%;
  }
}

@media (max-width: 1024px) {
  .header__Img::before {
    top: 124px;

    min-height: 16rem;
    width: 75.8%;
    left: 30%;
  }
}
@media (max-width: 768px) {
  .header__Info--Titulo {
    font-size: 1.5rem;
    text-align: center;
    font-weight: bold;
  }

  .header__Info--P {
    margin-top: 0 !important;
  }

  .header__Img::before {
    top: 86px;

    min-height: 13rem;
    width: 80%;
    left: 26%;
  }
}

@media (max-width: 425px) {
  .header__Info {
    padding-top: 4rem;
  }
  .header__Info--Titulo {
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
  }

  .header__Info--P {
    text-align: center;
  }
  .header__Img::before {
    top: 73px;

    min-height: 13rem;
    width: 72%;
    left: 28%;
  }
}

@media (max-width: 375px) {
  .header__Info {
    padding-top: 4rem;
  }
  .header__Info--Titulo {
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
  }

  .header__Info--P {
    text-align: center;
  }
  .header__Img::before {
    top: 73px;

    min-height: 11rem;
    width: 72%;
    left: 28%;
  }
}

@media (max-width: 320px) {
  .header__Info {
    padding-top: 4rem;
  }
  .header__Info--Titulo {
    font-size: 1.5rem;
    text-align: center;
    font-weight: bold;
  }

  .header__Info--P {
    text-align: center;
  }

  .header__Img::before {
    top: 72px;

    min-height: 10rem;
    width: 71%;
    left: 29%;
  }
}

.navbar{
    padding: .5rem 8.5rem;
}

.navbar-collapse{
    justify-content: flex-end;
}

nav .nav-link{
    font-weight: bold;
}
nav .nav-link:hover{
    color:hsl(0, 94%, 66%)!important;
    font-weight: bold;
}
@media(max-width:768px){
    .navbar{
        padding: .5rem 1rem;
    }
}

